
.share-tool{
	position: relative;
	background: #000;
	color:#fff;
	font-size: 18px;
	font-weight: normal;
	width: 100%;
	overflow: hidden;
	padding:35px 0;
	@include desktop {
		padding: 50px 0;
	}


	.wrapper{

		.back-link{
			padding-bottom: 25px;
			text-align: center;
			@include tablet {
				text-align: left;
			}

			@include desktop {
				padding-bottom:0;
			}
		}

		a{
			color: inherit;
			text-decoration: none;
			strong{
		 		color: #e60000;
		  	}
		  	position:relative;
		}

		a.chevron{
			@include icon-after("chevron-right.svg", 30px, 0 0.5em, -4px);
		}

	}

	.share-list{
		background: #1a1a1a;
		@include clearfix();

		@include tablet {
			display: block;
		}
		@include desktop{
			background: transparent;
		}


		h5{
			color: white;
			white-space: nowrap;
		}

		a{

			display: inline-block;
			margin: 3px;
			@include desktop {
					margin-right: 1em;
			}

			span{
				vertical-align: middle;
				display: none;
				@include desktop {
					display: inline-block;
					margin-left: 10px;
				}
			}

			&.email{
				position: relative;
				@include icon("envelope.svg", 55px);
				@include desktop {
					@include icon("envelope.svg", 27px);
				}
			}
			&.twitter{
				position: relative;
				@include icon("share_twitter.svg", 45px);
				@include desktop {
					@include icon("share_twitter.svg", 25px);
				}
			}
			&.linkedIn{
				position: relative;
				@include icon("share_linkedin.png", 45px);
				@include desktop {
					@include icon("share_linkedin.png", 25px);
				}
			}

			&:before{
				position: relative !important;
			}

			&:first-child{
				@include desktop {
					margin-left:30px;
				}
			}

			&:last-child{
				@include desktop {
					padding-right:30px;
				}
			}

		}


		.tcell{
			display: table-cell;
			display: inline-block;
			vertical-align: middle;
			&:last-child{
				float: right;
			}
			&:nth-child(2){
				height: 60px;
			}
			@include tablet {
				&:last-child{
					padding-left: 20px;
					float: none;
				}
				&:nth-child(2){
					height: 0px;
				}
			}

			@include desktop {
				text-align: right;
				&:last-child{
					padding-left: 0;
				}
			}
		}

	}
}

/* grid */
.share-tool{

	.wrapper{
		@include span(4 of 4);
		margin:0;
		width: 100%;
		@include tablet {
			@include span(8 of 8);
			width: 100%;
			margin:0
		}
		@include desktop {
			@include center(14, 16);
		}

		.back-link{
			@include span(4 of 4);

			@include desktop {
				@include span(6 of 14);
				margin-top: 0;
			}
		}
		.share-list{
			@include span(4 of 4);
			padding-left: 2.5%;
			padding-right: 2.5%;
			margin: 0;
			width: 100%;

			@include desktop {
				padding:0;
				width: auto;
				float:right;
			}

		}
	}
	.no-js &{
		display: none;
	}
}



