/* common styles */

// Ready
.ready-color {
	color: $ready-green;
}
.ready-bg {
	background: $ready-green;
	color: $mine-shaft;
}
.ready-border {
	border-color: $ready-green !important;
}
// Dynamic colors based on parent element
.ready {
	.readinessColor {
		color: $ready-green;
	}
	.readiness-bg {
