html.mobile-safari {
	.page-wrapper {
		&.lead-capture,
		&.full-report {
			// window scrolls
			height: auto !important;
		}

	}
}

html.no-js,
html.error {
	.page-wrapper {
		display: none;
	}
}

.page-wrapper {
	&.landing {
		// window scrolls
		overflow: auto;
	}

	&.survey {
		// sg-wrapper scrolls
		height:100%;
	}

	&.report {
		position: relative;
		margin:0;
		padding:0;
		width:100%;
		height:100%;
		overflow: auto;

		section.intro-summary {
			height: auto;
			overflow: visible;
		}

		section.pre-lead,
		section.post-lead,
		section.contact,
		footer.share-tool {
			display: none;
		}

		&.with-footer {
			height:100%;
			// scroll wrapper scrolls
			section.intro-summary {
				height: 100%;
				overflow: hidden;

				.scroll-wrapper {
					height: 100%;
					overflow: auto;
				}
				.full-report-footer {
					z-index:1000;
				}
			}
		}

		&.lead-capture {
			// page wrap scroll
			section.pre-lead,
			section.contact,
			footer.share-tool {
				display: block;
			}
		}

		&.full-report {
			// page wrap scroll
			section.post-lead,
			section.contact,
			footer.share-tool {
				display: block;
			}
		}
	}
}

// Base Styles
*{
	vertical-align: text-top;
	box-sizing: border-box;
	font-family:'Vodafone', sans-serif;
}

html, body {
	position: relative;
	margin: 0;
	padding: 0;
	width:100%;
	height:100%;
	min-height:100%;
	background: $alto;
	font-size: 24px !important;// important for survey gizmo overriding base size
	line-height: 24px !important;
	font-family:'Vodafone', sans-serif;
	&.survey {
		background: $light-black;
	}
}

// Common Attributes

.-underline{
    //todo still needs testing
    display: inline-block;
    &:after {
        content:" ";
        display:block;
        padding:0px 0;
        margin-top:-1px;
        line-height:1px;
        border-bottom:1px solid #fff;
    }
}

.-vertical-center {
    // parent element must have transform-style: preserve-3d;
    display:block;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}

.-debug p, .-debug h1, .-debug h2, .-debug h3 {
	background-color: rgba(255,255,255,0.1);
}

.-hide, .-hidden {
	display: none !important;
}

.capitalize {
	text-transform:capitalize;
}

.footer-button {
	text-align: center;
	padding: 10px 30px;
	margin: 15px;
	display:inline-block;
	background: $vod-red;
	font-size: 0.8em;
	@include tablet {
		font-size: 1em;
	}
}
