.full-report {
	@include span(full inside no-gutters);
	float: none;
	background: white;
}

.sector-wrapper {
	@include span(full inside no-gutters);
	text-align: center;
	overflow: hidden;
	padding-bottom: 30px;

	@include center(4, 4);
	@include tablet {
		@include center(8, 8);
	}
	@include desktop {
		@include center(14, 16);
	}

	p.intro {
		padding: 30px 0 0 0;
		text-align: center;
		@include center(4, 4);
		@include tablet {
			@include center(6, 8);
		}
		@include desktop {
			@include center(12, 14);
		}
	}
	.review-answers-expand,
	.review-answers-close {
		font-weight: bold;
		&:not(.open) {
			text-decoration: underline;
			cursor: pointer;
			padding-bottom: 40px;
		}
		.down-chevron,
		.up-chevron {
			position: relative;
			display: inline-block;
			height:15px;
			width: 0px;
		}
		.down-chevron {
			@include icon("small-chevron-down.svg", 20px, 0, 1px, 0, 0, 5px);
		}
		.up-chevron {
			@include icon("chevron-up.svg", 20px, 0, 0px, 0, 0, 5px);
		}
	}

	.review-answers-expand {
		padding: 20px 0;
		&.open {
			@extend %h3;
		}
	}

	.review-answers {
		height: auto;
		display: none;
		overflow: hidden;

		@include span(4 of 4);
		@include tablet {
			@include center(6, 8);
		}
		@include desktop {
			@include center(10, 12);
		}

		ul {
			list-style-type: none;
			padding:0;
			h5 {
				font-weight: bold;
			}
			li {
				margin: 0 0 25px 0;
				position: relative;
				text-align: center;
				padding: 0;
			}
		}
	}
}

.graph-col {
	margin-bottom: 30px;
	@include span(4 of 4);
	@include tablet {
		@include span(3 of 8);
	}
	@include desktop {
		@include span(5 of 14);
	}
	h3.sector-heading,
	h3.rb-score-heading {
		padding-bottom: 10px;
	}

	.score-container {
		&.sector-score {
			@include center(4, 4);
			@include tablet {
				@include center(3, 3);
			}
			@include desktop {
				@include center(4, 5);
			}
			max-width: 250px;
			min-width: 200px;
			margin: 0 auto;
			padding-bottom: 30px;
		}
		&.rb-score {
			@include center(3, 4);
			@include tablet {
				@include center(2, 3);
			}
			@include desktop {
				@include center(3, 5);
			}
			max-width: 180px;
			min-width: 150px;
			margin: 0 auto;
		}
	}
}

.product-col {
	@include span(4 of 4);
	@include tablet {
		@include span(5 of 8);
	}
	@include desktop {
		@include span(8 of 14);
	}

	.item {
		position: relative;
		min-height: 200px;
		width: calc(100% - 60px);
		margin: 20px 0;
		float: right;
		text-align: left;
		color: white;
		.select-product {
			cursor: pointer;
			position: absolute;
			height:60px;
			width: 60px;
			left: -60px;
			top: 0;
			background-color: $ship-grey;
			background-image: url('../img/icons/plus.png');
			background-repeat: no-repeat;
			background-position: 13px 13px;
		}
		.left-col {
			height: 100%;
			width: 100%;
			@include tablet {
				width: 67%;
			}
			float:left;
			padding: 20px 30px;
			h3 {
				cursor: pointer;
			}
			p {
				padding: 10px 0 40px;
			}
			a.link {
				@extend p;
				padding:0;
				text-decoration: none;
				font-weight: bold;
				color: white;
				position: absolute;
				bottom: 20px;
				left: 30px;
				right: 33%;
				display: block;
				@include icon('chevron-right.svg', 25px, 0, 0, 10px)
			}
		}
		.right-col {
			display:none;
			@include tablet {
				width: 33%;
				display: block;
			}
			position: absolute;
			top:0;
			right:0;
			bottom:0;
			&.image {
				vertical-align: top;
				background-size: cover;
				background-position: center;
				background-repeat: none;
				background-color: #333;
				border-left: 3px solid $vod-red;
			}
		}
		&.product {
			background: $ship-grey;
			h3, p {
				color: white;
			}
		}
		&.article {
			background: $iron;
			color: $ship-grey;
			a.link {
				color: $ship-grey;
				@include icon('chevron-right-grey.svg', 25px, 0, 0, 10px)
			}
			.select-product {
				display: none;
			}
		}
		&.selected {
			background: $vod-red;
			.select-product {
				background-color: $vod-red;
				//background-image: url('../img/icons/minus.png');
				background-image: none;
				@include icon("tick-white.svg", 50px, 0, 50%);
				&:before {
					left: 7px;
					margin-top: -3px;
					@extend .-vertical-center;
				}
			}
		}
	}
}
