%heading{
	font-family:'Vodafone', sans-serif;
	color: $mine-shaft;
}

%paragraph{
	font-family:arial,sans-serif;
	color: $mine-shaft;
}

@mixin paragraph {
	font-family:arial,sans-serif;
	color: $mine-shaft;
}

// Size Extenders

p, .p, %p1 {
	@extend %paragraph;
	font-size: 14px;
	line-height: 1rem;
}

h1, .h1, %h1{
	@extend %heading;
	font-weight: 200;
	font-size:2rem;
	line-height:1em;
	margin: 0;
}

h2, .h2, %h2 {
	@extend %heading;
	font-size: 1.2rem;
	line-height: 1em;
	margin:0;
}

h3, .h3, %h3{
	@extend %heading;
	font-size:0.9rem;
	line-height:1.2rem;
	margin: 0;
}

h4, .h4, %h4{
	@extend %heading;
	font-size: 18px;
	line-height:1rem;
}

h5{
	@extend %heading;
	font-size: 18px;
	line-height:1rem;
}

.-light, %-light{
	font-weight: 200;
}


// Breakpoints

@include tablet(){

	p, .p, %p1 {
		@extend %paragraph;
		font-size: 15px;
		line-height: 0.9rem;
	}

	h1, .h1, %h1{
		@extend %heading;
		font-size:4rem;
		line-height:1em;
		margin: 0;
	}

	h2, .h2, %h2 {
		font-size: 1.5rem;
		line-height: 1em;
	}

	h3, .h3, %h3{
		@extend %heading;
		font-size:1rem;
		line-height:1.3rem;
		margin: 0;
	}

	h4, .h4, %h4{
		@extend %heading;
		font-size: 19px;
		line-height:1.1rem;
		&.-small{
			font-size: 14px;
		}
	}

}
