@mixin opacity($opacity) {
  opacity: $opacity;
  $opacity-ie: $opacity * 100;
  filter: alpha(opacity=$opacity-ie); //IE8
}

@mixin mobileOnly {
  @include susy-media(0 767px) {
    @content;
  }
}

@mixin tablet {
  @include susy-breakpoint($tablet-width, $tablet-br) {
    @content;
  }
}

@mixin desktop {
  @include susy-breakpoint($desktop-width, $desktop-br) {
    @content;
  }
}

@mixin center($columns, $context: $total-columns) {
  @include span($columns of $context);
  margin: 0 auto;
  float: none;
}


@mixin icon_($icon,$size:40px,$margin: nil, $top:nil,$right:nil,$bottom:nil,$left:nil){

    background-image: url(#{"../img/icons/"+$icon+""});
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    width: $size;
    height: $size;
    @if $icon == nil{
      width:0px;
    }
    @if $top != nil{
      top: $top;
      position: absolute;
    }
    @if $right != nil{
      right: $right;
      position: absolute;
    }
    @if $bottom != nil{
      bottom: $bottom;
      position: absolute;
    }
    @if $left != nil{
      left: $left;
      position: absolute;
    }
    @if $margin != nil{
      margin: $margin;
    }
    line-height: 0px;
    content: " ";
    padding: 0;
    display: inline-block;
    vertical-align: middle;

}

@mixin icon($icon,$size:40px,$margin: 0, $top:0,$right:nil,$bottom:nil,$left:nil){
    &:before{
      @include icon_($icon,$size,$margin, $top,$right,$bottom,$left);
    }
}

@mixin icon-after($icon,$size:40px,$margin: nil, $top:nil,$right:nil,$bottom:nil,$left:nil){
    &:after{
      @include icon_($icon,$size,$margin, $top,$right,$bottom,$left);
    }
}

@mixin icon-position($position:nil) {
    &:before {
        @if $position=="center" {
            top: 50%;
            left: 50%;
            transform: translateY(-50%) translateX(-50%);
        }
        @if $position=="right middle" {
            top: 50%;
            right: 0px;
            transform: translateY(-50%);
        }
    }
}

@mixin icon-size($width:nil, $height:nil) {
    &:before {
        @if $width !=nil {
            width: $width;
        }
        @if $height !=nil {
            height: $height;
        }
    }
}