.capture-form {
	@include span(4 of 4);
	margin-bottom: (2*gutter(4));
	padding: gutter(4);

	@include tablet {
		@include center(8, 8);
	}

	@include desktop {
		@include center(12, 16);
	}

	h2 {
		text-align: center;
		color: #fff;
		@include squish(5%);
		color: $off-white-text;
		margin-bottom: 20px;
		font-weight: 200;
		strong{
			color: #fff;
		}
	}
	.subtext{
		color: #fff;
		font-size: 14px;
	}

	p.error{
		color: #e60000;
	}

	.first {
		width: 49% !important;
		float:left;
	}
	.second {
		width: 49% !important;
		float: right;
	}

	.row{
		@include desktop {
			input{
				&[type="text"] {
					width: 49%;
					display: table-cell;

					&:first-child{
						float: left;
					}
					&:last-child{
						float: right;
					}
				}
			}
		}
	}
	.terms-wrapper {
		padding: 20px 0;
		p{
			margin-bottom: 10px;
		}
		label {
			display: block;
			overflow: auto;
		}
		#dummy-checkbox {
			float: left;
			cursor: pointer;
			content: "";
			height: 40px;
			width: 40px;
			position:absolute;
			display: block;
			background: $mid-light-grey;
			border: 1px $silver;
		}
		.text {
			width: calc(100% - 40px);
			margin: 6px 0;
			padding-left: 20px;
			float: right;
		}
	}

	input {
		padding: 10px;
		margin: 10px 0;
		background: $mid-light-grey;
		color: #fff;
		font-size: 0.8em;
		line-height:1em;

		&[type="text"] {
			width: 100%;
			height: 3em;
			border: 1px solid $mid-light-grey;
			line-height: normal;

			&:focus{
				 outline: none !important;
				border: 1px solid $silver;
			}
		}
		@include placeholder {
			font-weight: 100;
			color: $silver;
		}


		&.error{
			border-color: #e60000;
		}
	}
	input[type="checkbox"] {
	    display:none;
	     &.error{
	    	+ label span{
				border-color: #e60000;
			}
		}
	}

	input[type="checkbox"] + label {
		color: $off-white-text;
		font-weight: normal;
		font-size: 14px;

		a{
			color: $off-white-text;
		}
	}

	input[type="checkbox"] + label div{
		padding-left: 50px;
		@include paragraph;
		line-height: 1.5em;
	}

	#dummy-checkbox.checked {
		border-color: $off-white-text;
		@include icon('form-tick.svg')
	}

	input[type="submit"] {
		display:block;
		cursor: pointer;
		border: none;
		background: $vod-red;
		color: white;
		padding: 1em 1em;
		@include tablet {
			padding: 1em 2em;
		}
		@include desktop {
			padding: 1em 3em;
		}
		text-align: center;
		font-size: 20px;
		margin: 0 auto;
	}
}

.general-error{
	text-align: center;
	color: #e60000;
	padding-bottom: 20px;
	&.hide{
		display: none;
	}
}


