.rhombus {
   	top: 0;
    left: 0;
    width: 86px;
    height: 60px;

    @include tablet {
        width: 160px;
        height: 112px;
    }
    @include desktop {
        width: 215px;
        height: 150px;
    }

    position: absolute;
    z-index: 1000;
    margin: 0;
    padding: 0;
    display: block;
    text-indent: -3000px;
    background-color: transparent;
    background-image: url("../img/voda_logo_main.png");
    background-repeat: no-repeat;
    background-size: 100%;
}

.arrow-cta {
	background-image: url("../img/icons/vodafone-down-cta.svg");
	background-repeat: no-repeat;
	background-size: contain;
	position: absolute;
	left: 50%;
	margin-left: -31px;
	width: 30px;
	height: 33px;
	@include animation(bounce 2s infinite);
}

.arrow-link {
	background-image: url("../img/icons/vodafone-down-cta.svg");
	background-repeat: no-repeat;
	background-size: contain;
	position: absolute;
	left: 50%;
	margin-left: -31px;
	width: 30px;
	height: 33px;
	@include animation(bounce 2s infinite);
}

._external-link{
	background-image: url("../img/icons/vodafone-down-cta.svg");
	background-repeat: no-repeat;
	background-size: contain;
	position: relative;
	width: 100px;
	height: 100px;
	background: red;
}


 .go-home{
        position: absolute;
        color: $mine-shaft;
        font-size: 12px;
        top: 10px;
        right: 10px;
        background: url("../img/icons/exit_link.png") no-repeat -34px 0;
        width: 32px;
        height: 32px;
        display: block;
        background-size: cover;
        background-position: 0 0;
        z-index:1;

        @include tablet {

            font-size: 18px;
            width: 45px;
            height: 45px;
            top: 20px;
            right: 20px;
        }

         @include desktop {
            overflow: hidden;
             background-position: -48px 0;
         }

        div{
            height: 100%;
            overflow: hidden;
            position: relative;
            top: 3px;
            right: 159px;
            width: 179px;
            @include tablet {
                top: 8px;
                right: 208px;
                width: 208px;
            }
        }

        .slide-out{
            display: block;
            overflow: hidden;
            text-align: right;
            position: absolute;
            width: 80%;
            left: 0;
			margin-top: 4px;
			white-space: nowrap;

            @include tablet {

                width: 95%;
            }
             @include desktop {
                display: none;
             }

        }

        @at-root html.has-touch & {
             @include desktop {
                overflow: visible;
                background-position: 0 0;
                .slide-out{
                    width: 95%;
                    display: inline-block;
                }
            }
        }


        &:hover{
            @include desktop {

                overflow: visible;
                background-position: 0 0;

                .slide-out {
                    display:block;
                    @include animation(slide-out 0.5s);
                }
            }
        }

    }

.debug-grid {
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0px;
	top: 0px;
	z-index:10;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	pointer-events: none;
}

.debug-grid.-on {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0px;
	left: 0px;
	$base-line-height: 24px;
	@include show-grid($mobile-br-debug);
	@include tablet {
		@include show-grid($tablet-br-debug);
	}
	@include desktop {
		@include show-grid($desktop-br-debug);
	}
}

h2.header-bar {
	position: relative;
	background: $ship-grey;
	color: white;
	text-align: center;
	padding: 24px 10px;
	width: 100%;
	font-weight: bold;
	&:after {
		content: "";
		position: absolute;
		left:calc(50% - 15px);
		bottom: -15px;
		border-left: 15px solid transparent;
		border-right: 15px solid transparent;
		border-top: 15px solid $ship-grey;
	}
}

.no-js-msg {
	.js & {
        display: none;
    }
}

.error-msg {
	display: none;
	.error & {
        display: block;
    }
}

.no-js-msg,
.error-msg {
    background-color: $vod-red;
    padding: 20px 30px;
    border-top: 4px solid #fff;
    position: relative;
    top: 50px;
    .text{
        padding: 10px 0 10px 85px;
        @include icon("no_js_alert.svg", 60px, nil, 20px, nil, nil, 30px);

        h3{
        	color: white;
            font-size: 20px;
            margin: 0;
        }
        p{
        	color: white;
           margin: 0;
        }
    }

    @include span(4 of 4);

    @include tablet {
        @include center(6, 8);
    }
    @include desktop {
        @include center(12, 16);
    }
}
