.sticky-nav {
	position: fixed;
	left: 0;
	right: 0;
	top:0;
	background-color: $mine-shaft;
	z-index: 999;
	text-align: center;
	height: 50px;
	opacity:0;
	display: none !important;
	@include desktop {
		display: block !important;
	}

	&.displayed {
		display: block;
	}
	.tabs-wrapper {
		display: inline-block;
		margin: 0 auto;
		height: 100%;
		overflow: hidden;
	}
	.tab {
		@extend p;
		padding: 0 20px;
		line-height: 50px;
		display: inline-block;
		color: $alto;
		background-color: $mine-shaft;
		cursor: pointer;
		&:hover {
			background-color: $emperor;
		}
		&.selected {
			background-color: $alto;
			color: $mine-shaft;
		}
	}
	.share-icon {
		position: absolute;
		width: 50px;
		top:0;
		bottom:0;
		right:0;
		left: auto;
		cursor: pointer;

		&:hover {
			background-color: $emperor;
		}

		&.selected {
			background-color: $emperor;
		}

		@include icon-after("share.png", 30px, 0, 10px, 10px, 10px, 10px);
	}
	.share-dropdown {
		position: absolute;
		width: 300px;
		top: 65px;
		right:0;
		left:auto;
		bottom:auto;
		background-color: $mine-shaft;
		opacity:0;
		display:none;
		&:before {
			content: "";
			position: absolute;
			right: 10px;
			top: -15px;
			bottom: auto;
			left:auto;
			border-left: 15px solid transparent;
			border-right: 15px solid transparent;
			border-bottom: 15px solid $mine-shaft;
		}
		p {
			color: $athens-grey;
			margin: 10px 20px;
		}
		&.displayed {
			display: block;
		}
		.share-button {
			position: relative;
			margin: 10px 20px;
			padding: 10px 15px;
			font-size: 14px;
			text-align: left;

			background-color: $athens-grey;
			color: $mine-shaft;

			outline: 1px solid $athens-grey;
			background-repeat: no-repeat;
			background-position: right 20px top 50%;

			&:hover{
				background-color: $iron;
				cursor: pointer;
			}
			&:active{
				outline: none;
			}
			&.email{
				display: block;
				@include icon-after("envelope-grey.svg", 40px, 0, 0px, 10px);
				&:after{
					height: 100%;
				}
				text-decoration: none;
			}
			&.link{
				@include icon-after("link-grey.png", 30px, 0, 0px, 15px);
				&:after{
					height: 100%;
				}
				margin-bottom: 20px;
			}
		}
	}
}
