.share-report {
	background-color: white;
	@include span(full inside no-gutters);
	@include clearfix();
	float:none;

	.share-report-inner {
		padding: 40px 0 100px 0;
		overflow: auto;
		@include span(4 of 4);
		@include tablet {
	        @include center(6, 8);
	    }
	    @include desktop {
	        @include center(8, 16);
	    }
	    @include clearfix();
	}

	h3 {
		text-align: center;
		padding: 20px 0;
	}

	.share-button {
		padding: 20px 25px;
		font-size: 14px;

		background: black;
		color: white;
		outline: 1px solid black;

		background-repeat: no-repeat;
		background-position: right 20px top 50%;
		position: relative;

		@include span(4 of 4);
		@include tablet {
	        @include span(3 of 6);
	    }
	    @include desktop {
	        @include span(4 of 8);
	    }
	    &:first-of-type {
	    	@include mobileOnly {
	    		margin-bottom: 20px;
	    	}
	    }

		&:hover{
			background-color: #333;
			cursor: pointer;
		}
		&:active{
			outline: none;
		}
		&.email{
			display: block;
			@include icon-after("envelope.svg", 40px, 0, 0px, 10px);
			&:after{
				height: 100%;
			}
			text-decoration: none;
		}
		&.link{
			@include icon-after("link.png", 30px, 0, 0px, 15px);
			&:after{
				height: 100%;
			}
		}
	}
}
