#survey-wrapper {
	@include container($mobile-cols nest);
	@include tablet {
		@include container($tablet-cols nest);
	}
	@include desktop {
		@include container($desktop-cols nest);
	}

	.sg-question {
		&.cols-two {
			&.sg-type-radio .sg-list,
			&.sg-type-checkbox .sg-list {
				li {
					@include span(4 of 4);
					clear: both !important;

				}

				@include tablet {
					@include squish(1 of 8);
					li {
						@include span(3 of 6);
						clear: none !important;
					}

				}
				@include desktop {
					@include squish(3 of 16);
					li {
						@include span(6 of 12);
						clear: none !important;
					}

				}
			}
		}

		&.cols-three{
			&.sg-type-radio .sg-list,
			&.sg-type-checkbox .sg-list {
				li {
					@include span(4 of 4);
					clear: none;
				}

				@include tablet {
					@include squish(1 of 8);
					li {
						@include span(6 of 6);
					}

				}
				@include desktop {
					@include squish(2 of 16);
					li {
						@include span(4 of 12);
					}
				}
			}
		}
		&.cols-more{
			&.sg-type-radio .sg-list,
			&.sg-type-checkbox .sg-list {
				li {
					@include span(2 of 4);
					clear: none;
				}

				@include tablet {
					@include squish(1 of 8);
					li {
						@include gallery(3 of 6);

					}

				}
				@include desktop {
					@include squish(2 of 16);
					li {
						@include gallery(4 of 12);

					}
				}
			}
		}
		&.cols-four{
			&.sg-type-radio .sg-list,
			&.sg-type-checkbox .sg-list {
				li {
					@include gallery(2 of 4);
					clear: none;
				}

				@include tablet {
					@include squish(1 of 8);
					li {
						@include gallery(3 of 6);

					}

				}
				@include desktop {
					@include squish(2 of 16);
					li {
						@include gallery(3 of 12);

					}
				}
			}
		}
		&.sg-type-menu {
			@include full;
			@include tablet {

			}
			@include desktop {
				@include center(12, 16);
			}
			.sg-question-title {
				@include span(4 of 4);
				@include tablet {
					@include center(7, 8);
				}
				@include desktop {
					@include center(14, 16);
				}
			}
			.sg-question-options {
				@include break;
				margin-left: 2.5%;

				position: relative;
				@include span(4 of 4);
				@include tablet {

				}
				@include desktop {

				}
				select{

				}
			}
			float: none;
			overflow: hidden;
		}
	}
	.sg-content-hook-2 {
		.sg-question-description {
			@include span(4 of 4);
			@include tablet {
				@include squish(1 of 8);
			}
			@include desktop {
				@include squish(4 of 16);
			}
		}
	}


}
