section.outro-summary{
	background-color: white;
	@include span(full inside no-gutters);
	float: none;
	overflow: auto;
	text-align: center;

	.outro-summary-wrapper {
		padding: 40px 0 20px 0;
		overflow:auto;
		@include span(4 of 4);
		@include tablet {
	        @include center(7, 8);
	    }
	    @include desktop {
	        @include center(14, 16);
	    }
	    .left-col,
	    .right-col {
	    	> h3 {
	    		margin-bottom: 10px;
	    	}
	    }

	    .left-col {
			@include span(4 of 4);
			@include tablet {
		        @include span(3 of 7);
		    }
		    @include desktop {
		        @include span(5 of 14);
		    }
		    margin-bottom: 50px;
	    }
	    .right-col {
			@include span(4 of 4);
			@include tablet {
				@include pre(1 of 5);
		        @include span(4 of 7);
		    }
		    @include desktop {
		        @include span(9 of 14);
		    }
	    }
	}

	.score-container {
		height: auto;
		@include tablet {
			height: 280px;
		}
		@include desktop {
			height: 240px;
		}
		.outer-line-label,
		.outer-circle-track {
			display: none;
		}

		&.original-rb-score {
			@include center(3, 4);
			@include tablet {
		        @include center(2, 3);
		    }
		    @include desktop {
		        @include center(2, 5);
		    }
		    max-width: 180px;
		    min-width: 150px;
			margin: 0 auto;
    	}

		&.potential-rb-score {
			@include center(4, 4);
			@include tablet {
		        @include center(3, 5);
		    }
		    @include desktop {
		        @include center(3, 9);
		    }
		 	max-width: 250px;
		 	min-width: 200px;
		 	margin: 0 auto;

		}
		.circle-graph-container {
			top:0;
			@include transform(none);
			@include tablet {
				top: 50%;
    			transform: translateY(-50%);
			}
		}
	}

	.sector-list {
		overflow:hidden;
		padding-top: 20px;
		@include span(4 of 4);
		@include tablet {
	        @include span(3 of 3);
	        margin-top: gutter(4);
	    }
	    @include desktop {
	        @include center(4, 5);
	        margin-top: gutter(4);
	    }
		.sector-box {
			@include span(4 of 4);
			margin: 0 gutter(4);
			@include tablet {
		        @include span(3 of 3);
		        margin: 0 gutter(4);
		    }
		    @include desktop {
		        @include span(4 of 4);
		        margin: 0 gutter(4);
		    }

			background: $iron;
			height: 72px;
			p {
				line-height: 72px;
			}
		}
	}

	.product-grid {
		overflow:hidden;
		padding-top: 20px;
		@include span(4 of 4);
		@include tablet {
	        @include span(4 of 4);
	    }
	    @include desktop {
	        @include center(8, 9);
	    }

		.product {
			@include span(4 of 4);
			margin: gutter(8);
			@include tablet {
		        @include span(4 of 4);
		        margin: gutter(8);
		    }
		    @include desktop {
		        @include span(4 of 8);
		        margin: gutter(8);
		        &.single {
		    		@include center(4, 8);
		    	}
		    }

		    position:relative;
		    opacity: 0.4;
		    background: $athens-grey;
		    overflow: visible;
		    height: 77px;

		    .product-info {
		    	width: calc(100% - 60px);
		    	float: right;
		    	@extend .-vertical-center;
		    	padding: 10px;
		    	cursor: pointer;
		    	h3 {
		    		padding-top: 5px;
		    		font-weight: bold;
		    	}
		    }

			.tick-box {
	    		background-color: $iron;
	    		position: relative;
	    		float: left;
	    		height:100%;
	    		width: 60px;
				background-image: url('../img/icons/plus.png');
				background-repeat: no-repeat;
				background-position: 50% 50%;
				cursor: pointer;
	    	}
		    &.selected {
		    	opacity:1;
				.tick-box {
					background-image: none;
					@include icon("tick.svg", 50px, 0, 50%);
					&:before {
						left: 7px;
						margin-top: -3px;
						@extend .-vertical-center;
					}
				}
		    }
		}
	}
}
