.contact {
	position: relative;
	background-color: #333333;
	overflow: visible;
	float: none;
	text-align: center;
	border-top: 3px solid $vod-red;
	padding: gutter(4);
	p {
		color: white;
	}

	h2 {
		color: white;
		padding-top: 0.2em;
		padding-bottom: 0.4em;
	}
	.contact-wrapper {
		margin-top: 20px;
		overflow: hidden;
		overflow-y: visible;

		.telephone-group {
			overflow: hidden;
			overflow-y: visible;
			width: 100%;
		}
	}

	.tel-wrapper {
		float: left;
	}

	.tel-heading {
		padding-top: 10px;
		margin-bottom: -6px;
	}

	.tel-title {
		font-weight: 700;
		padding: 1em 0 0 0;
		margin-bottom: -6px;

	}
	.telephone-group {
		padding-bottom: 1em;
	}

	.telephone-number {
		position: relative;
		display: table;
		background-color: #3f3f3f;
		font-weight: 700;
		overflow: visible;
		@extend .h3;
		margin: 10px 0;
		padding-right: 10px;
		width: 100%;
		.icon-wrapper {
			display: table-cell;
			vertical-align: middle;
			background-color: #393939;
			width: 20%;
			min-width: 80px;
			small {
				color: white;
				font-size: 0.5em;
				word-spacing: nowrap;
				padding: 6px 4px;
				vertical-align: top;
				line-height: 1.2em
			}
			img {
				vertical-align: middle;
				height: 50px;
				margin: 10px;
				max-width: 80px;
			}
		}
		.tel {
			color: white;
			display: table-cell;
			vertical-align: middle;
			text-align: center;
			padding: 20px 0 14px;
			width: 66%;
		}
	}
	@include desktop {
		padding: (2*gutter(10));
	}
}

.contact {
	width: 100%;
	.tel-wrapper {
		width: span(100%);
	}
	.tel-title {
		text-align:center;
		display: block;
	}
	.tel-heading {
		text-align: left;
	}
	.telephone-number {
		.icon-wrapper {
			width: 20%;
			.tel-title {
				display:none;
				text-align:center;
			}
		}
		.tel {
			@extend .h3;
		}
	}

	@include tablet {
		.telephone-group {
			@include center(6, 8);
			&:not(.one-tel-group) {
				@include span(4 of 8);
			}
		}
		.tel-wrapper {
			width: span(100%);
		}
		.tel-title {
			display: none;
		}
		.tel-heading {
			display: block;
			margin-left: (2*gutter(8));
		}
		.telephone-number {
			margin: 10px 10px;
			.icon-wrapper {
				width: 30%;
				.tel-title {
					display: block;
				}
			}
			.tel {
				@extend .h3;
			}
		}
	}
	@include desktop {
		.contact-wrapper {
			@include center(12, 16);
		}

		.telephone-group {
			@include center(10, 12);
			&:not(.one-tel-group) {
				@include span(6 of 12);
			}
		}

		.tel-wrapper {
			width: span(100%);
		}

		.tel-title {
			display: none;
		}
		.telephone-number {
			margin: 10px 0;
			.icon-wrapper {
				.tel-title {
					display: block;
				}
			}
			.tel {
				font-size: 1.6em;
			}
		}
	}
}
