.industry-results {
	@include span(4 of 4);
	height:100%;

	margin-bottom:20px;
	position: relative;

	@include tablet {
		@include span(5 of 8);
	}
	@include desktop {
		@include span(9 of 14);
	}

	h3 {
		opacity: 0;
		padding-bottom: 30px;
	}
	.wrapper {
		@include desktop {
			@include center(7, 9);
		}
	}
	.disclaimer{
		opacity: 0;
		color: $mine-shaft;
		text-align:center;
		position: relative;
		z-index: 10;
		padding: 20px 0 10px;
	}

	.industry-chart {
		position: relative;
		width: 100%;
		padding: 0;
		opacity:0;
		width:100%;
		height: 250px;
		overflow:visible;
		margin-bottom:10px;

		@include tablet {
			display: block !important;
		}
		.col-wrapper {
			display:inline-block;
			text-align: center;
			background-color: transparent;
			position: relative;
			height: 90%;
			min-height:205px;

			width: span(25% - (gutter(4)));
			margin: 0 (gutter(4)/2);
			@include tablet {
				width: span(25% - (2*gutter(8)));
				margin: 0 gutter(8);
			}
			@include desktop {
				width: span(25% - (2*gutter(12)));
				margin: 0 gutter(12);
			}
			.chart-data-item {
				position: absolute;
				top:100%;
				padding:8px 1px;
				text-align: center;
				width: 100%;
				word-wrap: break-word;
				text-overflow: ellipsis;
				line-height: 1em;
				font-size: 16px;
				&.-small {
					@include mobileOnly {
						font-size:12px;
						font-weight: 400;
					}
				}
			}
		}
	}
	.no-js &{
		display: none;
	}
}

.chart-col {
	position: absolute;
	display: block;
	bottom: 5px;
	width: 100%;
	height:0;
	overflow: visible;
	transform:scaleY(0);
	transform-origin: bottom;

	.annotated {
		position: absolute;
		left:0;
		right:0;
		top: -44px;
		color: $mine-shaft;
		background:transparent;
		padding: 0 2px;
		height: 26px;
		font-size: 0.5em;
		line-height:1em;
		@include tablet {
			font-size: 0.65em;
		}
		&:before{
			content: "";
			position:absolute;
			width: 0;
			height: 0;
		  	border-left: 10px solid transparent;
		  	border-right: 10px solid transparent;
		  	border-top: 15px solid $mine-shaft;
	    	left:50%;
	    	margin-left:-10px;
			bottom:-15px;
			animation:annotated-arrow 0.4s ease-in-out infinite alternate;
		}
	}
}

@-webkit-keyframes annotated-arrow{
	0%{
		top:20px;
	}
	100%{
		top:23px;
	}
}


@-moz-keyframes annotated-arrow{
	0%{
		top:20px;
	}
	100%{
		top:23px;
	}
}


@keyframes annotated-arrow{
	0%{
		top:20px;
	}
	100%{
		top:23px;
	}
}
