.page-wrapper.landing {
	h1 {
		font-size: 30px;
		@include tablet {
			font-size: 45px;
		}
	}
}

.get-started {
	display: block;
	@include span(4 of 4);
	text-align: left;
	padding-top:20px;
	@include tablet {
		padding-top:30px;
	}

	p {
		font-weight: bold;
		padding: 20px 0;
	}
	.red-button {
		display: inline-block;
		color: white;
		background-color: $vod-red;
		padding: 10px 30px;
		text-decoration: none;
		font-size: 18px;
		margin: 0 10px 20px 0;
	}
	.clock-label {
		position: relative;
		line-height:1.2em;
		font-size: 16px;
		display: inline-block;
		.clock-icon {
			@include icon("clock.svg", 40px, 0, -11px);
		}
		.text {
			position: relative;
			margin-left: 55px;
			display:inline-block;
		}
	}
}

section.introduction {
	height: auto;
	background-color: $alto;
	background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 50%;

    background-image: url("/img/background-img/landing_bkg_small.jpg");
    @include tablet {
        background-image: url("/img/background-img/landing_bkg.jpg");
    }
	.center-wrapper {
		overflow: visible;
		padding: 70px 0 30px 0;
		@include center(4, 4);
		@include tablet {
			padding: 100px 0px 0px 0px;
	        @include center(7, 8);
	    }
	    @include desktop {
	    	padding: 80px 0px 0px 0px;
	        @include center(12, 16);
	    }

	    h1 {
	    	display: block;
	    	width: 100%;
	    	padding-top: 50px;
	    	padding-bottom: 15px;

	    	// float none required to stick image to bottom of right-col div
			float: none !important;

	    	@include tablet {
	    		padding-bottom: 20px;
	    	}
	    	@include desktop {
	    		display: none;
	    	}
	    }

	}

	.table-wrapper {
		display: table-row !important;

	}

	.left-col {
		// float none required to stick image to bottom of div
		float: none !important;

		h1.desktop-only {
			display: none;
			width:100%;
		}
		display: table-cell;
		@include span(4 of 4);
		@include tablet {
	        @include span(3 of 7);
	        p {
	        	font-size: 18px;
	        }
	    }
	    @include desktop {
	        @include span(7 of 12);
	        p {
	        	font-size: 18px;
	        }
	        h1.desktop-only {
				display:block;
				margin: 0;
		    }
	    }

	    p {
	    	padding: 10px 0;
	    }
	}
	.right-col {
		position:relative;
		width: 100%;
		display: none;
		text-align: right;

		// float none required to stick image to bottom of div
		float: none !important;

		@include span(4 of 4);
		@include tablet {
			display: table-cell;
			vertical-align: bottom;
	        @include span(4 of 7);
	    }
	    @include desktop {
	        @include span(5 of 12);
	    }

	    img {
	    	vertical-align: bottom;
			display:none;
	    	max-width: 500px;
	    	max-height: 450px;
	    	@include tablet{
	    		display:block;
	    		max-width: 100%;
	    	}
	    	@include desktop {
	    		max-height: 550px;
	    	}
	    	margin: 0 auto;
	    }
	}
	.get-started {
		width: 100%;
		margin: 0;
		padding: 30px 0;

		@include tablet {
			.clock-label {
				display: inline-block;
			}
	    }

	    @include desktop {
	        text-align: left;
	    }
	}
}

section.who-else,
section.how-we-calculate {
	background-color: white;

	p.intro {
		padding: 10px 0 40px 0;
		text-align: center;
	}

	.wrapper {
		overflow: auto;
		padding: 30px;
		@include center(4, 4);
		@include tablet {
	        @include center(7.5, 8);
	    }
	    @include desktop {
	        @include center(12, 16);
	    }
	}
	.get-started {
		text-align:center;
		@include tablet {
	        @include span(6 of 6);
	    }
	    @include desktop {
	        @include span(12 of 12);
	    }
	}
	h4 {
		font-weight: bold;
	}
}

section.how-we-calculate {
	.left-col {
		@include span(4 of 4);
		@include tablet {
	        @include span(3 of 7.5);
	    }
	    @include desktop {
	        @include span(4 of 12);
	    }
	   	text-align: center;
	   	.graph-container {
	   		padding:10px;
	   		.circle-graph-container {
	   			max-width: 220px;
	   			.outer-circle-track {
	   				display:none;
	   			}
	   		}
	   	}
	}
	.right-col {
		@include span(4 of 4);
		@include tablet {
	        @include span(4.5 of 7.5);
	    }
	    @include desktop {
	        @include span(7 of 12);
	        @include pre(1 of 12);
	    }
	    p {
	    	padding-bottom: 20px;
	    }
	}

	h3 {
	   	padding-bottom: 20px;
	}
}

section.who-else {
	.case-studies-wrapper {
		display: table-row;
		border-collapse:separate;
	}

	.case-study {
		display: table-cell;
		margin-bottom: 20px;
		@include span(4 of 4);
		@include tablet {
	        @include span(2.5 of 7.5);
	        float: none;
	        border-left: 8px solid white;
	        border-right: 8px solid white;
	    }
	    @include desktop {
	        @include span(4 of 12);
	        float: none;
	    }
	    background-color: $ship-grey;
	    .text {
	    	padding: 15px;
	    	text-align: left;
	    }
	    h4 {
	    	color: white;
	    	font-size: 19px;
	    }
	    p {
	    	color: white;
	    	padding: 10px 0;
	    }
	    .header-img {
	    	height: auto;
	    	width: 100%;
	    	border-bottom: 3px solid $vod-red;
	    }
	}
}
