/* survey specific styles */

#survey-wrapper {
	$select-row-size: 70px;

	position: relative;
	top:0;
    width: 100%;
    height: 100%;
    min-height: 100%;
    overflow: auto;
    -webkit-overflow-scrolling: touch;

	.bg-img-wrapper {
		position:absolute;
		top:0;
		left:0;
		right:0;
		bottom:0;
		background-color: $light-black;
		background-size: cover;
		background-repeat: no-repeat;
		//background-attachment: fixed;
		//background-image: url("../img/background-img/survey-bg-1.jpg");
		opacity:0;
		z-index:2;
	}

	.sg-page-title {
		display:none;
	}

	.sg-content {
		opacity:0;
	}

	.sg-content-hook-1 {
	    width: 100%;
	    overflow: hidden;
	    padding-top: 20px;

	    @include tablet {
	    	padding-top: 50px;
	    }

		h2{

			height: 90px;
			background-size: auto 60px;
			text-align: center;
			color: white;
			font-size: 20px;
			margin:0;
			background-repeat: no-repeat;
			background-position: center center;
			text-indent: -2000em;

 			@include tablet {
				height: 140px;
				background-size: inherit;
				margin-bottom: 15px;
			}
		}

	}

	.sg-survey {
		position: relative;
        width: 100%;
        height:100%;
        overflow: auto;
        z-index:3;

        *{
        	word-break: break-word;
        }

		.sg-body {
			padding-bottom: 0;
			position: relative;
			background-color: transparent;
			background-size: cover;
			min-height: 100%;
			width: 100%;
        	height:100%;
        	overflow: auto;

			.sg-survey-form {
				width: 100%;
        		height:100%;
        		overflow: auto;
				&:after {
				  content: "";
				  display: table;
				  clear: both;
				}
			}
		}
	}

	.sg-wrapper {
		width: 100%;
        height:100%;
        overflow: auto;
		background: transparent !important;
		padding-top:0;

		.sg-header {
			display: none;
		}
		.sg-content {
			position: relative;
			width: 100%;
	        height:100%;
	        overflow: visible;
			padding: 0;
			background: transparent;
		}
	}
	.quote-hook{
		width: 100%;
		overflow: hidden;
		padding-bottom: 100px;

		 @include tablet {
		 	padding-bottom: 180px;
		 }
		.outer{
			padding-top: 30px;
		}
		.inner {
			padding: 0 0 0 100px;
			background: transparent url("../img/icons/sv_quote_left.png") no-repeat left top;
			min-height: 50px;

		    @include span(4 of 4);

		    @include tablet {
		        @include center(6, 8);
		    }
		    @include desktop {
		        @include center(12, 16);
		    }
		}
		p {
			background: transparent url("../img/icons/sv_quote_right.png") no-repeat right top;
			padding-right: 100px;
			color: #fff;
			margin: 0 auto;
			text-align: center;
			min-height: 50px;
		}
	}
	.sg-question-set {
		.sg-question {
			border: none;
			padding-top: 0;
			margin-top: 0;

			.sg-question-title{
				padding: 0 2.5%;
				padding-bottom: 30px;
				@extend %heading;
				font-weight: 200;
			}
			.sg-question-title,
			.sg-question-title label {
				text-align: center;
				color: white;
				font-size: 30px;
				@extend %heading;
				font-weight: 200;
				 @include tablet {
				 	font-size: 60px;
				 }

				&.small-font{
					font-size: 20px;
					 @include tablet {
					 	font-size: 31px;
					 }
				}
				br {
					line-height: 20px;
					font-size: 20px;
				}
				em {
					line-height: 20px;
					font-size: 14px;
					font-style: normal;
					display: block;
					 @include tablet {
					 	font-size: 20px;
					 }

				}
			}
			.sg-question-options {
				//margin:0;
				padding:0;
			}
		}

		.sg-required-icon {
			color: $vod-red;
		}
	}

	.fixed-footer {
		height: 60px;
		background: black;
		@include tablet {
			min-height: 90px;
			height: 15%;
			max-height: 110px
		}

		position: fixed;
		bottom: 0;
		left: 0;
		margin: 0;
		padding: 0;
		width: 100%;
		display: block;
		z-index:4;

		@include tablet {
			background: rgba(0,0,0,0.6);
		}
		.progress-bar {
			@extend .-vertical-center;
			display: block !important;
			height: 100%;
			width:60%;
			@include tablet {
				height: 10px;
				width: 50%;
			}

			position: relative;
			left:auto;
			bottom:auto;
			margin: 0 auto;

			.progress-bar-text {
				display:none;
				white-space: nowrap;
				line-height:20px;
				@include mobileOnly {
					left:10px !important;
				}
				top: 50%;
				margin-top:-10px;
				@include tablet {
					top: -14px;
					left: 0;
					margin-left: -16px;
					font-size: 20px;
					@include transition(left 1.0s ease-in-out);
				}

				position: absolute;
				background: transparent;
				width: auto;
				padding: 0;
				color: white;
				text-align: left;
				&:after {
					content: " Progress";
				}
			}

			.progress-bar-background {
				border-radius: 0;
				height: 100%;
				background: transparent;

				@include tablet {
					border-radius: 5px;
					height: 10px;
					background: $light-black;
				}

				padding: 0;
				margin: 0;
				width: 100%;
				border: none;

				.progress-bar-inner {
					width:0;
					height: 100%;
					@include tablet {
						border-top-left-radius: 5px;
						border-bottom-left-radius: 5px;
					}
					background: #b60a0a;
					@include transition(width 1.0s ease-in-out);
				}
			}
		}
	}

	.sg-footer {
		@extend .fixed-footer;
		background:transparent!important;
		z-index:5;
		.sg-button-bar {
			margin:0;
			.sg-next-button, .sg-submit-button {
				@extend .next-button;
				display:none;
				opacity:1;
				text-indent: -3000px;
				@include tablet {
					text-indent: -110px;
				}
				&.enabled {
					display: block;
					opacity:1;
				}

			}
			.sg-back-button {
				@extend .back-button;
				opacity:1;
				text-indent: 3000px;
				word-break: initial;
				@include tablet {
					text-indent: 40px;
				}
			}
		}
		.sg-progress-bar {
			display:none;
		}
	}

	.sg-button {
		background-color: $dark-grey;
		&:hover, &:active, &:focus {
			background-color: $dark-grey;
		}
		@include tablet {
			background-color: transparent;
			&:hover, &:active, &:focus {
				background-color: transparent;
			}
		}

		margin:0;
		padding:0 10%;
		position: absolute;
		width: 20%;
		height: 100%;
		line-height:100%;
		color: white;
		border-radius: 0;
		border:none;
		opacity: 0.2;
		background-position: center center;
		background-repeat: no-repeat;

		&.next-button {
			right: 0;
			background-image: url("../img/icons/chevron-right-small.png");
			&:hover {
				background-image: url("../img/icons/chevron-right-small-green.png");
			}
			@include tablet {
				background-image: url("../img/icons/chevron-right.png");
				&:hover {
					background-image: url("../img/icons/chevron-right-green.png");
				}
			}
		}
		&.back-button {
			left: 0;
			background-image: url("../img/icons/chevron-left-small.png");
			@include tablet {
				background-image: url("../img/icons/chevron-left.png");
			}
		}
		&.enabled {
			opacity: 1;
			color: rgba(255,255,255,1);
		}
	}


	.sg-question {
		.sg-question-options{
			label{
				@extend %heading;
				font-weight: 200;
			}
			&.large-font {
				label {
					font-size: 40px;
				}
			}
			&.small-font {

				 label {
					font-size: 25px;
				}
			}

		}
		.sg-question-description {

		}

		&.cols-more {
			.small-font label {
				font-size: 15px;
			}
		}

		&.sg-type-radio,
		&.sg-type-checkbox {
			.sg-list {
				li {
					position:relative;
					cursor:pointer;
					text-decoration: none;
	  				padding:0;
	  				margin: 0;
	  				margin-bottom:15px;
	  				overflow: visible;
				}
			}
			label {
				margin: 0 0 20px 0;
				padding: 10px 20px;
				float: left;
				text-align: center;
				cursor:pointer;
				background: white;
				width: 100%;
				height:100%;
				min-height:55px;
				transform-style: preserve-3d;

				&:before {
					content: '';
				}

			}
			input {
				//display: none;
			}
		}

		&.sg-type-radio,
		&.sg-type-checkbox {
			label {
				border-top: 3px solid #cbcbcb;
				&:hover {
					border-color: #999;
					background: #ccc;
				}
			}
			input:checked + label {
				position: relative;
				color: white;
				border-color: $selected !important;
				background: #292929;
				&:before {
					position: absolute;
					margin:0;
					padding:0;
					top: -16px;
					left: auto;
					right: 14px;
					display: block;
					width: 30px;
					height: 30px;
					background: transparent url("../img/icons/icon-ticked-green.png") 0 0 no-repeat;
				}
			}
			.sg-question-options {
				overflow: visible;
			}
		}

		&.sg-type-menu {
			.sg-question-options {
				position: relative;
				padding: 0;
				background: white;
				min-height: $select-row-size;
				@include desktop {
					@include span(8 of 12);
					@include pre(2 of 12);
				}
				display: inline-block;

				&:after {
					content: '';
					position: absolute;
					top: 0;
					right: 0;
					height: $select-row-size;
					width: $select-row-size;
					display: block;
					border-left: 1px solid #cbcbcb;
					background: white url('../img/icons/small-chevron-down.png') center center no-repeat;
					cursor: pointer;
					z-index:0;
				}
				select {
					position: absolute;
					top: 0;
					left: 0;
					height: $select-row-size;
					width: 100%;
					padding: 0 18px;
					z-index: 9;
					outline: none;
					line-height: $select-row-size;
					border: none;
					border-left: 3px solid #cbcbcb;
					border-radius: 0;
					appearance: none;
					color: #4b4b4b;
					cursor: pointer;
					white-space:nowrap;
					overflow: hidden;
					padding-right: $select-row-size;
					text-overflow: ellipsis;
				}
			}
		}
	}




	.activetrigger, .trigger {
		position: absolute;
		display: block;
		width: 100%;
		height: $select-row-size;
  		padding-right: $select-row-size;
  		border-left: 3px solid #cbcbcb;
		z-index:1;
  		overflow: hidden;
		padding: 0.7rem;
		text-decoration: none;
		color: $mine-shaft;
		line-height: $select-row-size*0.5;
		white-space: nowrap;
		overflow: hidden;
  		text-overflow: ellipsis;
  		padding-right: $select-row-size;
  		&.clicked{
  			color: $mine-shaft;
  			border-color: $selected;
  		}
	}


	.dropcontainer {
		position: relative;
		background: $mine-shaft;
		margin-top: $select-row-size;

		.dropdownhidden {
			display: none;
		}

		.dropdownvisible {
			height: auto;
		    // height: 300px;
    		// overflow-y: scroll;
		}

		ul{
			border-top: none;
			list-style-type: none;
			padding: 0;
			margin: 0;
			width: 100%;
			z-index: 100;

			li {
				padding: 1rem;
				width: 100%;
				min-height: $select-row-size;
				border-bottom: 1px solid $light-text;
				cursor: pointer;

				&:nth-child(odd){
					background-color: $mid-dark-grey;
				}
				&:hover {
					background-color: #444;
				}

				&:last-child{
					border-bottom: none;
				}

				&:first-child{
					display: none;
				}

				&:hover{
					outline: none;
				}

				a{
					color: white;
					text-decoration: none;
					&:active{
						text-decoration: none;
					}

				}
			}

		}
	}
}


#survey-wrapper {
  	@for $i from 1 through 20 {
	    &.question-#{$i} {
	    	.bg-img-wrapper{
	    		background-image: url("../img/background-img/q#{$i}_small.jpg");
				@include desktop {
					background-image: url("../img/background-img/q#{$i}_large.jpg");
				}
			}
			.sg-body .sg-content-hook-1 {
		    	h2 {
	    			background-image: url("../img/question-numbers/#{$i}.png");
	   			}
	   		}
	    }
	}
}

.spinner {
	@extend .-vertical-center;
	position: absolute;
	left:0;
	right:0;
	margin:0 auto;
  	width: 70px;
  	text-align: center;
  	z-index:1;
}

.spinner > div {
  width: 18px;
  height: 18px;
  background-color: $light-grey;

  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
}

.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% { -webkit-transform: scale(0) }
  40% { -webkit-transform: scale(1.0) }
}

@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  } 40% {
    -webkit-transform: scale(1.0);
    transform: scale(1.0);
  }
}



