.full-report-footer {
	position: absolute;
	transform-style: preserve-3d;
	bottom:0;
	width: 100%;
	min-height:100px;
	max-height:130px;
	border-top: 5px solid $ready-green;
	background-color: rgba(0, 0, 0, 0.75);
	text-align: center;
	z-index:100;
	color: white;
	cursor: pointer;
	opacity:0;
	h2 {
		color: white;
		font-size: 1em;
		margin: 10px 0 0 0;
	}
	@include tablet {
		min-height:80px;
		max-height:130px;
		h2 {
			color: white;
			display: inline-block;
			font-size: 1.4em;
			margin: 20px 0 0 0;
		}
	}
}

section.pre-lead {
	background: $mine-shaft;
	padding: 10px 0 30px 0;
	overflow:auto;
	h2 {
		text-align: center;
		color: white;
	}
	.no-js &{
		display: none;
	}
}

section.post-lead {
	opacity: 0;
	background: $alto;
}
