.spinner {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 70px;
  text-align: center;
  z-index: 1; }

.spinner > div {
  width: 18px;
  height: 18px;
  background-color: #d1d1d1;
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both; }

.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s; }

.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s; }

@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0); }
  40% {
    -webkit-transform: scale(1); } }

@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0); }
  40% {
    -webkit-transform: scale(1);
    transform: scale(1); } }

.full-report-footer {
  position: absolute;
  transform-style: preserve-3d;
  bottom: 0;
  width: 100%;
  min-height: 100px;
  max-height: 130px;
  border-top: 5px solid #a8b400;
  background-color: rgba(0, 0, 0, 0.75);
  text-align: center;
  z-index: 100;
  color: white;
  cursor: pointer;
  opacity: 0; }
  .full-report-footer h2 {
    color: white;
    font-size: 1em;
    margin: 10px 0 0 0; }
  @media (min-width: 768px) {
    .full-report-footer {
      min-height: 80px;
      max-height: 130px; }
      .full-report-footer h2 {
        color: white;
        display: inline-block;
        font-size: 1.4em;
        margin: 20px 0 0 0; } }

