.progress-container {
	position: relative;
	margin-top: 20px;
	display: block;
}

.circle-graph-container {
	position: relative;
	margin: 0 auto;
	text-align: center;
	max-width: 280px;
	min-width: 160px;

	&.outer-labelled {
		margin-bottom: 40px !important;
	}

	&.ready {
		svg.circle-graph path.inner-line {
			stroke: $ready-green;
		}
	}
	&.almost-ready {
		svg.circle-graph path.inner-line {
			stroke: $almost-ready-yellow;
		}
	}
	&.unready {
		svg.circle-graph path.inner-line {
			stroke: $unready-amber;
		}
	}
	&.at-risk {
		svg.circle-graph path.inner-line {
			stroke: $at-risk-red;
		}
	}

	svg.circle-graph path.outer-line {
		&.ready {
			stroke: $ready-green;
		}
		&.almost-ready {
			stroke: $almost-ready-yellow;
		}
		&.unready {
			stroke: $unready-amber;
		}
		&.at-risk {
			stroke: $at-risk-red;
		}
	}

	.circle-ratio-squared {
		content: "";
		display: inline-block;
		margin-top: 100%;
	}
	.svg-container {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		vertical-align: top;
		transform-style: preserve-3d;
	}
	.inner-text {
		position: absolute;
		left: 0;
		right: 0;
	}
	// svg:
	svg {
		overflow: visible;
	}
	svg path {
		fill: none;
	}
	svg.circle-graph {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
	}
	svg.circle-graph path {
		stroke: black;
		stroke-width: 2;
		opacity: 1;
		&.circle-track {
			stroke: $alto-dark;
			stroke-linecap: square;
			stroke-width: 2;
		}
		&.inner-line {
			stroke-dashoffset: 308%;
			stroke-linecap: square;
			stroke-dasharray: 0 100%; // sets the line to empty
			stroke-width: 2;
		}
		&.outer-line {
			stroke-dasharray: 1, 1;
			stroke-width: 2;
			stroke: $emperor;
		}
		&.outer-line-mask {
			stroke-width: 10;
			fill: black;
			stroke: white;
		}

		&.outer-circle-track {
			stroke-dasharray: 1, 1;
			stroke-width: 2;
			stroke: $gallery;
		}
	}
}

.score-container.rb-score-main,
.graph-wrapper {
	.circle-graph-container {
		.percent,
		.label,
		.circle-track,
		.inner-line,
		.outer-circle-track,
		.outer-line-label {
			opacity:0;
		}
	}
}



.not-applicable {
	margin-bottom:0;
	.outer-line-label,
	.outer-circle-track,
	.outer-line,
	.inner-line {
		display:none;
	}
}

.inner-text {
	.percent {
		@extend .h1;
		display:block;
		color: $cod-grey;
		text-align: center;
		font-weight: bold;
		line-height: 1em;

		font-size: 60px;
		@include tablet {
			font-size: 64px;
		}
		@include desktop {
			font-size: 74px;
		}
		&.small {
			font-size: 56px;
			@include tablet {
				font-size: 60px;
			}
			@include desktop {
				font-size: 64px;
			}
		}
	}
	.label {
		display:block;
		color: $cod-grey;
		line-height: 1em;
		font-size: 20px;
		margin: 0 auto;
		&.small {
			max-width: 140px;
			font-size: 17px;
		}
		&.strong {
			font-weight: bold;
		}
		&.fixed-height {
			height: 1em;
			overflow: visible;
		}
	}
}

.outer-line-label {
	color: $cod-grey;
	position: absolute;
	line-height: 1em;
	bottom: -1.5em;
	left:0;
	right:0;
	font-size: 15px;
}
