section.intro-summary {
	.scroll-wrapper {
		width:100%;
		position: relative;
		padding-top: 40px;
		@include desktop {
			padding-top: 50px;
		}
		@include clearfix();
	}

	position: relative;
	padding:0;
	margin:0;
	background: $alto;
	.js & {
		background: -moz-linear-gradient(top, $alto 0%, #ffffff 100%);
		background: -webkit-gradient(left top, left bottom, color-stop(0%, $alto), color-stop(100%, #ffffff));
		background: -webkit-linear-gradient(top, $alto 0%, #ffffff 100%);
		background: -o-linear-gradient(top, $alto 0%, #ffffff 100%);
		background: -ms-linear-gradient(top, $alto 0%, #ffffff 100%);
		background: linear-gradient(to bottom, $alto 0%, #ffffff 100%);
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='$alto', endColorstr='#ffffff', GradientType=0 );
	}

	h1{
		opacity: 0;
		text-align: center;
		margin:0 0 20px 0;
		padding: 0 2.5%;
		font-size: 30px;
		@include center(3, 4);
		@include tablet {
			font-size: 45px;
			margin-bottom: 30px;
			@include center(6, 8);
		}
		@include desktop {
			@include center(12, 16);
		}
	}

	h3, h4 {
		.no-js &{
			color: #fff;
		}
		text-align: center;
	}

	.no-js &{
		height: 100%;
	}

	.top-graph-wrapper {
		overflow: hidden;
		padding-top: 30px;
		@include tablet {
			padding-top: 50px;
		}
		@include desktop {
			@include center(14, 16);
		}
	}
}

.results-score {
	position: relative;
    vertical-align: top;
    height:100%;
	margin-bottom: 40px;
    overflow: hidden;

    @include span(4 of 4);
    @include tablet {
        @include span(3 of 8);
    }
    @include desktop {
        @include span(5 of 14);
    }

	&.no-industry {
		@include tablet {
	        @include center(3, 8);
	    }
	    @include desktop {
	        @include center(6, 14);
	    }
	}

    h3 {
        opacity: 0;
        margin: 0;
        line-height: 1.2;
        font-weight: normal;
        @include tablet {
            font-size: 24px;
        }
    }
    .score-container.rb-score-main {
        padding-top: 30px;
		max-width: 300px;
        @include center(3, 4);
	    @include tablet {
	        @include center(3, 3);
	    }
	    @include desktop {
	        @include center(4, 5);
	    }
    }
    .no-js & {
        display: none;
    }
}

.sector-summary-container {
	padding-top:30px;
	overflow: hidden;
	@include span(full inside no-gutters);
	@include tablet {
		@include span(full inside no-gutters);
		@include center(7.5, 8);
	}
	@include desktop {
		@include center(14, 16);
	}
	h3 {
		opacity: 0;
		padding-bottom: 30px;
	}
	.wrapper {
		overflow: auto;
		opacity:0;
		h4 {
			padding-bottom: 20px;
		}
		@include span(4 of 4);
		@include tablet {
			@include span(2.5 of 7.5);
			&:nth-child(3) {
				border-left: 3px solid $iron;
				border-right: 3px solid $iron;
			}
		}
		@include desktop {
			@include span(4.666 of 14);
		}
		.graph-wrapper {
			overflow: visible;
			margin-bottom:10px;
		}
		.circle-graph-container {
			@include center(2, 4);
			@include tablet {
				@include center(2, 2.5);
			}
			@include desktop {
				@include center(2, 4.5);
			}
		}
		p {
			padding: 10px 0 30px 0;
			text-align:center;
			@include center(3, 4);
			@include tablet {
				@include center(2.5, 2.5);
			}
			@include desktop {
				@include center(4, 4.5);
			}
		}
	}
	padding-bottom: 40px;
}

.page-wrapper.with-footer .sector-summary-container {
	padding-bottom: 130px;
}
